import { Container } from "reactstrap";
import React, { useEffect, useContext, useState } from "react";
import './style.css'
import { AuthContext } from "../../../context/authContext";
import axios from "axios";
import API_URL from "../../../api/Router";
import { Link } from 'react-router-dom'
import DataTable from 'react-data-table-component';
import './style.css'
import { toast } from "react-toastify";
import toastOption from "../../../config/toast";
import Loading from 'react-loading';


const RentalHistory = () => {
    const { currentToken, userDecode } = useContext(AuthContext);
    const [data, setData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalRows, setTotalRows] = useState(0);
    const dataPerPage = 10;
    const [isLoading, setIsLoading] = useState(true);


    const getRentalHistory = async () => {
        try {
            const config = {
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${currentToken}`,
                },
            };

            const res = await axios.get(`${API_URL}/api/bookings/hotelier`, config);
            console.log(res)
            if (res.status === 200) {
                setData(res.data)
            }
        } catch (error) {
            console.log(error.message);
        }
    }

    const handlePageChange = (page) => {
        setCurrentPage(page);
    };

    const formatDate = (data) => {
        const date = new Date(data);
        if (date instanceof Date && !isNaN(date.getTime())) {
            return date.toISOString().substring(0, 10);
        } else {
            return null;
        }
    }

    const formatMoney = (money) => {
        return money.toLocaleString('vi', { style: 'currency', currency: 'VND' })
    }

    const getStatusColor = (status) => {
        if (status === 'Completed') {
            return 'green';
        } else if (status === 'Cancel') {
            return 'red';
        } else {
            return 'gray';
        }
    }

    const actions = (row) => {
        if (row.bookingStatus === 'Processing') {
            return (
                <div>
                    <button class="btn btn-sm btn-outline-success mx-1" onClick={() => handleConfirmRental(row._id)}>
                        <i class="ri-check-line"></i>
                    </button>
                    <button class="btn btn-sm btn-outline-danger mx-1" onClick={() => handleCancelRental(row._id)}>
                        <i class="ri-close-line"></i>
                    </button>
                    <button class="btn btn-sm btn-outline-primary mx-1" onClick={() => handleDetailRental(row._id)}>
                        <i class="ri-edit-box-line"></i>
                    </button>
                </div>
            )
        } else {
            return (
                <button class="btn btn-sm btn-outline-primary mx-1" onClick={() => handleDetailRental(row._id)}>
                    <i class="ri-edit-box-line"></i>
                </button>
            );
        }
    }

    const handleConfirmRental = async (id) => {
        try {
            const config = {
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${currentToken}`,
                },
            };

            const res = await axios.get(`${API_URL}/api/bookings/${id}/return`, config);
            console.log(res)
            if (res.status === 200) {
                setData(list => updateList(res.data, list))
                toast.success('Confirm Successfully!', toastOption)
            }
        } catch (error) {
            console.log(error.message);
        }
    }

    const updateList = (newItem, currentList) => {
        const list = [...currentList];
        console.log(list);
        const isExist = list.find(item => item._id === newItem._id);
        if (!isExist) return [...list, newItem];
        const indexOfItem = list.findIndex(item => item._id === newItem._id);
        list[indexOfItem] = newItem;
        return list;
    }

    const handleCancelRental = async (id) => {
        alert('Not support this function')
    }

    const handleDetailRental = async (id) => {
        alert('Not support this function')

        // try {
        //     const config = {
        //         headers: {
        //             "Content-Type": "application/json",
        //             Authorization: `Bearer ${currentToken}`,
        //         },
        //     };

        //     const res = await axios.get(`${API_URL}/api/bookings/bookingDetails/${id}`, config);
        //     console.log(res)
        //     if (res.status === 200) {
        //         setData(res.data)
        //     }
        // } catch (error) {
        //     console.log(error.message);
        // }
    }

    const customStyles = {
        rows: {
            style: {
                minHeight: '72px', // override the row height
            },
        },
        headCells: {
            style: {
                paddingLeft: '8px', // override the cell padding for head cells
                paddingRight: '8px',
                borderRight: '1px solid #ccc',
            },
        },
        cells: {
            style: {
                paddingLeft: '8px', // override the cell padding for data cells
                paddingRight: '8px',
                borderRight: '1px solid #ccc'
            },
        },
    };

    const columns = [
        {
            name: 'No. ',
            cell: (row, index) => index + 1,
            width: "80px",
            center: true,
        },
        {
            name: 'License Plate',
            selector: 'licensePlate',
            center: true,
        },
        {
            name: 'Date Start',
            selector: 'bookingStart',
            cell: (row) => formatDate(row.bookingStart),
            sortable: true,
            center: true,

        },
        {
            name: 'Date End',
            selector: 'bookingEnd',
            cell: (row) => formatDate(row.bookingEnd),
            sortable: true,
            center: true,

        },
        {
            name: 'Total Price',
            selector: 'totalPrice',
            cell: (row) => formatMoney(row.totalPrice),
            sortable: true,
            center: true,

        },
        {
            name: 'Status',
            selector: 'bookingStatus',
            sortable: true,
            cell: (row) => <div style={{ color: getStatusColor(row.bookingStatus) }}>{row.bookingStatus}</div>,
            center: true,
        },
        {
            name: 'Action',
            cell: (row) => actions(row),
            center: true
        }
    ];

    useEffect(() => {
        const timer = setTimeout(() => {
            setIsLoading(false);
        }, 1500);

        return () => clearTimeout(timer);
    }, []);

    useEffect(() => {
        getRentalHistory();
    }, [])

    return (
        <Container className="mb-5">
            <h3 className="mt-5">Rental History </h3>

            {isLoading ? <Loading type="spin" color="#000" /> : (!data
                ? <p>You have never booked, just bring your ass to travel and rent a car. <Link to="/vehicles" style={{ textDecoration: 'underline !important' }}>Booking now!</Link></p>
                : <div className="App">
                    <DataTable
                        customStyles={customStyles}
                        columns={columns}
                        data={data}
                        pagination
                        paginationTotalRows={totalRows}
                        paginationPerPage={dataPerPage}
                        paginationComponentOptions={{
                            rowsPerPageText: 'Rows per page:',
                            rangeSeparatorText: 'of',
                            noRowsPerPage: false,
                            selectAllRowsItem: false,
                            selectAllRowsItemText: 'All',
                        }}
                        onChangePage={handlePageChange}
                    />
                </div>)
            }

        </Container>
    )
}

export default RentalHistory